import { useState } from "react";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../Utils/Functions/localTime";
import { imageShowUrl, pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";
import style from "../LmsManager.module.css";
import { useTranslation } from "react-i18next";
import LmsLeaveForDate from "../LmsLeaveRequest/LmsLeaveForDate";
const LmsRecommendationAuthorityHistoryCard = ({ leave, whichAccess }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);

  const onToggle = () => {
    setOpen((pre) => !pre);
  };

  return (
    <>
      <div className={style.lmsr_card}>
        <div className={style.lmsrc_card}>
          <div className={style.lmsrc_card_inner}>
            <img
              src={
                leave?.staff?.staffProfilePhoto
                  ? `${imageShowUrl}/${leave?.staff?.staffProfilePhoto}`
                  : `${assestsFeedUrl}/feed-user-avatar.svg`
              }
              alt="staff icon"
            />
            <div className={style.lmsrci_text}>
              <h6>{`${leave?.staff?.staffFirstName} ${
                leave?.staff?.staffMiddleName
                  ? leave?.staff?.staffMiddleName
                  : ""
              } ${leave?.staff?.staffLastName}`}</h6>
              <p>
                {whichAccess === "Sactioning" ? (
                  <>
                    {t("sanction_on")}{" "}
                    {localTimeFormat(leave?.sanction?.sanction_on, "LT")},{" "}
                    {dayjs(leave?.sanction?.sanction_on).format("DD MMM YYYY")}
                  </>
                ) : whichAccess === "Reviewing" ? (
                  <>
                    {t("reviewed_on")}
                    {localTimeFormat(leave?.review?.review_on, "LT")},{" "}
                    {dayjs(leave?.review?.review_on).format("DD MMM YYYY")}
                  </>
                ) : (
                  <>
                    {t("recomend_on")}{" "}
                    {localTimeFormat(leave?.recommend?.recommend_ont, "LT")}
                    {", "}
                    {dayjs(leave?.recommend?.recommend_ont).format(
                      "DD MMM YYYY"
                    )}
                    {", "}
                  </>
                )}
                <span className={style.lmsrc_date} onClick={onToggle}>
                  {t("for_dates")}
                </span>
              </p>
              <p>
                {whichAccess === "Sactioning" ? (
                  <>
                    {t("reviewing_by")}
                    {`${leave?.review?.review_by?.staffFirstName ?? ""} ${
                      leave?.review?.review_by?.staffMiddleName ?? ""
                    } ${leave?.review?.review_by?.staffLastName ?? ""}`}
                  </>
                ) : whichAccess === "Reviewing" ? (
                  <>
                    {t("recomending_by")}
                    {`${leave?.recommend?.recommend_by?.staffFirstName ?? ""} ${
                      leave?.recommend?.recommend_by?.staffMiddleName ?? ""
                    } ${leave?.recommend?.recommend_by?.staffLastName ?? ""}`}
                  </>
                ) : null}
              </p>
            </div>
          </div>
        </div>
        <div
          className={style.lmsrci_text}
          style={{
            marginTop: "0.5rem",
          }}
        >
          <h6
            style={{
              fontWeight: "400",
            }}
          >
            {t("leave_type")} {" : "}
            {leave?.leave_type}
          </h6>
        </div>
        <div
          className={style.lmsrci_text}
          style={{
            marginTop: "0.5rem",
          }}
        >
          <h6
            style={{
              fontWeight: "400",
              whiteSpace: "break-spaces",
            }}
          >
            {t("reason")} {" : "}
            {leave?.reason}
          </h6>
        </div>
        {leave?.attach && (
          <div
            className={style.lmsrci_text}
            style={{
              marginTop: "0.5rem",
            }}
          >
            <h6
              style={{
                fontWeight: "400",
                whiteSpace: "break-spaces",
              }}
            >
              {t("attachment")} {" : "}
              <a
                href={`${pdfCdnUrl}/${leave?.attach}`}
                target="_blank"
                rel="noopener noreferrer"
                style={{
                  textDecoration: "none",
                }}
              >
                <span className={style.lmsrc_date}>{t("view_attachment")}</span>
              </a>
            </h6>
          </div>
        )}
      </div>
      {open && <LmsLeaveForDate onClose={onToggle} allDate={leave?.date} />}
    </>
  );
};

export default LmsRecommendationAuthorityHistoryCard;
